/**
 * Initialize the router
 *
 * @author roland@elefunds.de (Roland Luckenhuber)
 */
define('tests/testMain',[
    'qunit',
    'tests/model/donation'
], function (QUnit, donationTest) {
    'use strict';
    return {
        init: function () {
            QUnit.stop();

            // start QUnit.
            QUnit.load();
            QUnit.start();

            // run the tests.
            donationTest.run();
        }
    };
});